<template><section><html><head></head><body><h1>Cesium3DTileset</h1>
<p>The <code v-pre="">vc-primitive-tileset</code> component is used to load a 3D Tiles tileset, used for streaming massive heterogeneous 3D geospatial datasets.</p>
<h2>Example</h2>
<h3>Load a Cesium3DTileset with <code v-pre="">vc-primitive-tileset</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-tileset :url="url" @ready-promise="readyPromise" @initial-tiles-loaded="initialTilesLoaded" @all-tiles-loaded="allTilesLoaded" @load-progress="loadProgress" @tile-failed="tileFailed" @tile-load="tileLoad" @tile-unload="tileUnload" @tile-visible="tileVisible">
        </vc-primitive-tileset>
      </vc-viewer>
      <div class="demo-tool">
        <span>Switch Source</span>
        <md-select v-model="url" placeholder="Switch Source">
          <md-option v-for="item in options" :key="item.value" :value="item.value">
            {{item.label}}
          </md-option>
        </md-select>
      </div>
    </div>
  </template>
  
</doc-preview>
