        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
      },
      <span class="hljs-title function_">readyPromise</span>(<span class="hljs-params">tileset</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>
        viewer.<span class="hljs-title function_">zoomTo</span>(tileset, <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">HeadingPitchRange</span>(<span class="hljs-number">0.0</span>, -<span class="hljs-number">0.5</span>, tileset.<span class="hljs-property">boundingSphere</span>.<span class="hljs-property">radius</span> * <span class="hljs-number">2.0</span>))
      },
      <span class="hljs-title function_">allTilesLoaded</span>(<span class="hljs-params"></span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'All tiles are loaded'</span>)
      },
      <span class="hljs-title function_">initialTilesLoaded</span>(<span class="hljs-params"></span>) {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'Initial tiles are loaded'</span>)
