          <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'Stopped loading'</span>)
          <span class="hljs-keyword">return</span>
        }

        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'Loading: requests: '</span> + numberOfPendingRequests + <span class="hljs-string">', processing: '</span> + numberOfTilesProcessing)
      },
      <span class="hljs-title function_">tileFailed</span>(<span class="hljs-params">error</span>) {
