<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>The url to a tileset JSON file.</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code>Determines if the tileset will be shown.</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Matrix4</td>
<td>Matrix4.IDENTITY</td>
<td><code v-pre="">optional</code> A 4x4 transformation matrix that transforms the tileset's root tile.</td>
</tr>
<tr>
<td>shadows</td>
<td>ShadowMode</td>
<td>ShadowMode.ENABLED</td>
<td><code v-pre="">optional</code> Determines whether the tileset casts or receives shadows from each light source.</td>
</tr>
<tr>
<td>maximumScreenSpaceError</td>
<td>Number</td>
<td>16</td>
<td><code v-pre="">optional</code> The maximum screen space error used to drive level of detail refinement.</td>
</tr>
<tr>
<td>maximumMemoryUsage</td>
<td>Number</td>
<td>512</td>
<td><code v-pre="">optional</code> The maximum amount of memory in MB that can be used by the tileset.</td>
</tr>
<tr>
<td>cullWithChildrenBounds</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> Optimization option. Whether to cull tiles using the union of their children bounding volumes.</td>
</tr>
<tr>
<td>cullRequestsWhileMoving</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Optimization option. Don't request tiles that will likely be unused when they come back because of the camera's movement. This optimization only applies to stationary tilesets.</td>
</tr>
<tr>
<td>cullRequestsWhileMovingMultiplier</td>
<td>Number</td>
<td><code v-pre="">60.0</code></td>
<td><code v-pre="">optional</code> Optimization option. Multiplier used in culling requests while moving. Larger is more aggressive culling, smaller less aggressive culling.</td>
</tr>
<tr>
<td>preloadWhenHidden</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> Preload tiles when tileset.show is false. Loads tiles as if the tileset is visible but does not render them.</td>
</tr>
<tr>
<td>preloadFlightDestinations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Optimization option. Preload tiles at the camera's flight destination while the camera is in flight.</td>
</tr>
<tr>
<td>preferLeaves</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> Optimization option. Prefer loading of leaves first.</td>
</tr>
<tr>
<td>dynamicScreenSpaceError</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> Optimization option. Reduce the screen space error for tiles that are further away from the camera.</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorDensity</td>
<td>Number</td>
<td>0.00278</td>
<td><code v-pre="">optional</code> Density used to adjust the dynamic screen space error, similar to fog density.</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorFactor</td>
<td>Number</td>
<td>4.0</td>
<td><code v-pre="">optional</code> A factor used to increase the computed dynamic screen space error.</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorHeightFalloff</td>
<td>Number</td>
<td>0.25</td>
<td><code v-pre="">optional</code> A ratio of the tileset's height at which the density starts to falloff.</td>
</tr>
<tr>
<td>progressiveResolutionHeightFraction</td>
<td>Number</td>
<td><code v-pre="">0.3</code></td>
<td><code v-pre="">optional</code> Optimization option. If between (0.0, 0.5], tiles at or above the screen space error for the reduced screen resolution of progressiveResolutionHeightFraction*screenHeight will be prioritized first. This can help get a quick layer of tiles down while full resolution tiles continue to load.</td>
</tr>
<tr>
<td>foveatedScreenSpaceError</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Optimization option. Prioritize loading tiles in the center of the screen by temporarily raising the screen space error for tiles around the edge of the screen. Screen space error returns to normal once all the tiles in the center of the screen as determined by the Cesium3DTileset#foveatedConeSize are loaded.</td>
</tr>
<tr>
<td>foveatedConeSize</td>
<td>Number</td>
<td><code v-pre="">0.1</code></td>
<td><code v-pre="">optional</code> Optimization option. Used when Cesium3DTileset#foveatedScreenSpaceError is true to control the cone size that determines which tiles are deferred. Tiles that are inside this cone are loaded immediately. Tiles outside the cone are potentially deferred based on how far outside the cone they are and their screen space error. This is controlled by Cesium3DTileset#foveatedInterpolationCallback and Cesium3DTileset#foveatedMinimumScreenSpaceErrorRelaxation. Setting this to 0.0 means the cone will be the line formed by the camera position and its view direction. Setting this to 1.0 means the cone encompasses the entire field of view of the camera, disabling the effect.</td>
</tr>
<tr>
<td>foveatedMinimumScreenSpaceErrorRelaxation</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> Optimization option. Used when Cesium3DTileset#foveatedScreenSpaceError is true to control the starting screen space error relaxation for tiles outside the foveated cone. The screen space error will be raised starting with tileset value up to Cesium3DTileset#maximumScreenSpaceError based on the provided Cesium3DTileset#foveatedInterpolationCallback.</td>
</tr>
<tr>
<td>foveatedInterpolationCallback</td>
<td>Function</td>
<td></td>
<td><code v-pre="">optional</code> Optimization option. Used when Cesium3DTileset#foveatedScreenSpaceError is true to control how much to raise the screen space error for tiles outside the foveated cone, interpolating between Cesium3DTileset#foveatedMinimumScreenSpaceErrorRelaxation and Cesium3DTileset#maximumScreenSpaceError</td>
</tr>
<tr>
<td>foveatedTimeDelay</td>
<td>Number</td>
<td><code v-pre="">0.2</code></td>
<td><code v-pre="">optional</code> Optimization option. Used when Cesium3DTileset#foveatedScreenSpaceError is true to control how long in seconds to wait after the camera stops moving before deferred tiles start loading in. This time delay prevents requesting tiles around the edges of the screen when the camera is moving. Setting this to 0.0 will immediately request all tiles in any given view.</td>
</tr>
<tr>
<td>skipLevelOfDetail</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre="">optional</code> Optimization option. Determines if level of detail skipping should be applied during the traversal.</td>
</tr>
<tr>
<td>baseScreenSpaceError</td>
<td>Number</td>
<td>1024</td>
<td><code v-pre="">optional</code> When skipLevelOfDetail is true, the screen space error that must be reached before skipping levels of detail.</td>
</tr>
<tr>
<td>skipScreenSpaceErrorFactor</td>
<td>Number</td>
<td>16</td>
<td><code v-pre="">optional</code> When skipLevelOfDetail is true, a multiplier defining the minimum screen space error to skip. Used in conjunction with skipLevels to determine which tiles to load.</td>
</tr>
<tr>
<td>skipLevels</td>
<td>Number</td>
<td>1</td>
<td><code v-pre="">optional</code> When skipLevelOfDetail is true, a constant defining the minimum number of levels to skip when loading tiles. When it is 0, no levels are skipped.</td>
</tr>
<tr>
<td>immediatelyLoadDesiredLevelOfDetail</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> When skipLevelOfDetail is true, only tiles that meet the maximum screen space error will ever be downloaded.</td>
</tr>
<tr>
<td>loadSiblings</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> When skipLevelOfDetail is true, determines whether siblings of visible tiles are always downloaded during traversal.</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>ClippingPlaneCollection</td>
<td></td>
<td><code v-pre="">optional</code> The ClippingPlaneCollection used to selectively disable rendering the tileset.</td>
</tr>
<tr>
<td>classificationType</td>
<td>ClassificationType</td>
<td></td>
<td><code v-pre="">optional</code> Determines whether terrain, 3D Tiles or both will be classified by this tileset. See Cesium3DTileset#classificationType for details about restrictions and limitations.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Ellipsoid</td>
<td>Ellipsoid.WGS84</td>
<td><code v-pre="">optional</code> The ellipsoid determining the size and shape of the globe.</td>
</tr>
<tr>
<td>pointCloudShading</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Options for constructing a PointCloudShading object to control point attenuation based on geometric error and lighting.</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Cartesian2</td>
<td>new Cartesian2(1.0, 1.0)</td>
<td><code v-pre="">optional</code> Scales the diffuse and specular image-based lighting from the earth, sky, atmosphere and star skybox.</td>
</tr>
<tr>
<td>lightColor</td>
<td>Cartesian3</td>
<td></td>
<td><code v-pre="">optional</code> The color and intensity of the sunlight used to shade models.</td>
</tr>
<tr>
<td>luminanceAtZenith</td>
<td>Number</td>
<td><code v-pre="">0.2</code></td>
<td><code v-pre="">optional</code> The sun's luminance at the zenith in kilo candela per meter squared to use for this model's procedural environment map.</td>
</tr>
<tr>
<td>sphericalHarmonicCoefficients</td>
<td>Array</td>
<td><code v-pre="">optional</code> The third order spherical harmonic coefficients used for the diffuse color of image-based lighting.</td>
<td></td>
</tr>
<tr>
<td>specularEnvironmentMaps</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> A URL to a KTX file that contains a cube map of the specular lighting and the convoluted specular mipmaps.</td>
</tr>
<tr>
<td>backFaceCulling</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td></td>
</tr>
<tr>
<td>debugHeatmapTilePropertyName</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> The tile variable to colorize as a heatmap. All rendered tiles will be colorized relative to each other's specified variable value.</td>
</tr>
<tr>
<td>debugFreezeFrame</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> For debugging only. Determines if only the tiles from last frame should be used for rendering.</td>
</tr>
<tr>
<td>debugColorizeTiles</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre="">optional</code> For debugging only. When true, assigns a random color to each tile.</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td>false</td>
