<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>allTilesLoaded</td>
<td></td>
<td>The event fired to indicate that all tiles that meet the screen space error this frame are loaded. The tileset is completely loaded for this view.This event is fired at the end of the frame after the scene is rendered.</td>
</tr>
<tr>
<td>initialTilesLoaded</td>
<td></td>
<td>The event fired to indicate that all tiles that meet the screen space error this frame are loaded. This event is fired once when all tiles in the initial view are loaded.This event is fired at the end of the frame after the scene is rendered.</td>
</tr>
<tr>
<td>loadProgress</td>
<td></td>
<td>The event fired to indicate progress of loading new tiles. This event is fired when a new tile is requested, when a requested tile is finished downloading, and when a downloaded tile has been processed and is ready to render.The number of pending tile requests, numberOfPendingRequests, and number of tiles processing, numberOfTilesProcessing are passed to the event listener.This event is fired at the end of the frame after the scene is rendered.</td>
</tr>
<tr>
<td>tileFailed</td>
<td></td>
<td>The event fired to indicate that a tile's content failed to load.If there are no event listeners, error messages will be logged to the console.</td>
</tr>
<tr>
<td>tileLoad</td>
<td></td>
<td>The event fired to indicate that a tile's content was loaded.The loaded Cesium3DTile is passed to the event listener.</td>
</tr>
<tr>
<td>tileUnload</td>
<td></td>
<td>The event fired to indicate that a tile's content was unloaded.The unloaded Cesium3DTile is passed to the event listener.This event is fired immediately before the tile's content is unload</td>
</tr>
<tr>
<td>tileVisible</td>
<td></td>
<td>This event fires once for each visible tile in a frame. This can be used to manually style a tileset.The visible Cesium3DTile is passed to the event listener.</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse is pressed on this primitive.</td>
